











































































import Vue from "vue";
// @ts-ignore
import { VueCropper } from "vue-cropper";
export default Vue.extend({
  components: {
    VueCropper
  },
  props: {
    className: {},
    filename: {
      default: "NONAME"
    },
    value: {
      type: String,
      default: null
    }
  },
  data() {
    return {
      model: false,
      modelSrc: "",
      crap: false,
      show: true,
      img: "",
      file: null,
      time: ""
    };
  },
  methods: {
    startCrop() {
      // start
      this.crap = true;
      // @ts-ignore
      this.$refs.cropper.startCrop();
    },
    stopCrop() {
      //  stop
      this.crap = false;
      // @ts-ignore
      this.$refs.cropper.stopCrop();
    },
    clearCrop() {
      // clear
      // @ts-ignore
      this.$refs.cropper.clearCrop();
    },
    refreshCrop() {
      // clear
      // @ts-ignore
      this.$refs.cropper.refresh();
    },
    changeScale(num: number) {
      num = num || 1;
      // @ts-ignore
      this.$refs.cropper.changeScale(num);
    },
    finish(type: string) {
      // 输出
      // var test = window.open('about:blank')
      // test.document.body.innerHTML = '图片生成中..'
      if (type === "blob") {
        // @ts-ignore
        this.$refs.cropper.getCropBlob(data => {
          console.log(data);
          // this.model = true;
          this.modelSrc = window.URL.createObjectURL(data);
          this.upload(data);
        });
      } else {
        throw "base 64 not implement";
        // // @ts-ignore
        // this.$refs.cropper.getCropData(data => {
        //   // this.model = true;
        //   this.modelSrc = data;
        //   this.$emit("input", data);
        // });
      }
    },
    upload(blob: Blob) {
      console.log(blob, typeof blob);
      let data = new FormData();
      data.append("image", blob, this.filename);
      this.$store.dispatch("upload", data).then(url => {
        console.log("FINISH");
        this.$emit("input", url);
        this.modelSrc = ""; //.replace("http:", "https:");
        this.time = "?t=" + new Date().getTime();
      });
    },
    realTime(data: any) {
      console.log("REALTIME", data);
    },
    // down(type: string) {
    //   // event.preventDefault()
    //   var aLink = document.createElement("a");
    //   aLink.download = "demo";
    //   // 输出
    //   if (type === "blob") {
    //     this.$refs.cropper.getCropBlob(data => {
    //       this.downImg = window.URL.createObjectURL(data);
    //       aLink.href = window.URL.createObjectURL(data);
    //       aLink.click();
    //     });
    //   } else {
    //     this.$refs.cropper.getCropData(data => {
    //       this.downImg = data;
    //       aLink.href = data;
    //       aLink.click();
    //     });
    //   }
    // },
    uploadImg(e: any) {
      console.log("UPLOAD IMAGE");
      var file = e.target.files[0];
      if (!/\.(gif|jpg|jpeg|png|bmp|GIF|JPG|PNG)$/.test(e.target.value)) {
        alert("no image select");
        return false;
      }
      var reader = new FileReader();
      reader.onload = e => {
        let data =
          // @ts-ignore
          typeof e.target.result === "object"
            ? // @ts-ignore
              // @ts-ignore
              window.URL.createObjectURL(new Blob([e.target.result]))
            : // @ts-ignore
              // @ts-ignore
              e.target.result;
        this.modelSrc = data;
      };
      // 转化为base64
      // reader.readAsDataURL(file)
      // 转化为blob
      reader.readAsArrayBuffer(file);
    },
    imgLoad(msg: any) {
      console.log(msg);
    }
  }
});
