

































































interface InputField {
  label: string;
  type: string;
  value?: any;
}

import Vue from "vue";
// @ts-ignore
import draggable from "vuedraggable";
import imageUpload from "./imageUpload.vue";
import _ from "lodash";
import File from "./File.vue";

export default Vue.extend({
  components: {
    draggable,
    imageUpload,
    File
  },
  data() {
    let layout = require("./input_form.js").default;
    return {
      form_result: null as any | null,
      form_layout: layout as InputField[],
      result: null
    };
  },
  async mounted() {
    this.update_result();
    if (this.auth) {
      if (!this.user) {
        await this.$store.dispatch("fetchUser");
      }
      this.form_result = this.user;
    }
  },
  computed: {
    user(): object {
      return this.$store.getters.user;
    },
    auth(): boolean {
      return this.$store.getters.auth;
    },
    transform_to_array(): any[] {
      if (this.form_result === null) return [];
      let arr = _.flatten(
        this.form_layout.map(({ label, type }: any) => {
          if (!label) return [];
          if (type != "rank") {
            let res = _.get(this.form_result, label);
            return [res != undefined ? res : "-"];
          } else {
            return this.form_result[label];
          }
        })
      );
      return arr;
    }
  },
  methods: {
    update_result() {
      for (let { label, type, value } of this.form_layout) {
        if (!label) continue;
        if (type == "rank") {
          this.form_result[label] = value.map((x: any) =>
            typeof x == "object" ? x.name : x
          );
        }
      }
      _.map(this.form_result, (v, k) => {
        if (k == "คำนำหน้า") {
          // @ts-ignore
          this.form_result["เพศ"] = { นาย: "ชาย", นางสาว: "หญิง" }[v] || "";
        }
      });
      this.form_result = this.form_result;
      this.$forceUpdate();
    },
    async submit(e: Event) {
      e.preventDefault();
      const loadingComponent = this.$loading.open({
        container: null
      });
      this.$store
        .dispatch("submit", this.form_result)
        .then(() =>
          this.$toast.open({
            type: "is-success",
            message: "success submition",
            position: "is-top",
            duration: 5000
          })
        )
        .catch(result => {
          this.$toast.open({
            type: "is-danger",
            message: result.response.data || "unexpected error",
            position: "is-top",
            duration: 5000
          });
          if (result.code == 405) {
            window.location.reload(true);
          }
        })
        .finally(loadingComponent.close);
    }
  },
  watch: {
    form_layout: {
      handler() {
        this.update_result();
      },
      deep: true
    },
    form_result: {
      handler() {
        this.update_result();
      },
      deep: true
    }
  }
});
